.statistics {
    width: 268px;
    height: 130px;
    box-shadow: 1px 1px 3px black;
    margin: auto;
    margin-bottom: 30px;
}

.title {
    height: 20px;
    text-align: center;
    color: #475965;
    text-transform: uppercase;
    padding-top: 30px;
    font-size: 16px;
}

.stat-list {
    display: flex;
    padding-left: 0;
}

.item {
    width: 67px;
    height: 60px;
    display: flex;
    flex-direction: column;
}

.item:nth-child(1) {
    background-color: #4fc4f6;
    color: #fff;
}

.item:nth-child(2) {
    background-color: #a33cf2;
    color: #fff;
}

.item:nth-child(3) {
    background-color: #e64c65;
    color: #fff;
}

.item:nth-child(4) {
    background-color: #21b8c6;
    color: #fff;
}

.item:nth-child(5) {
    background-color: #b58b36;
    color: #fff;
}

.label {
    margin-right: auto;
    margin-left: auto;
    margin-top: 11px;
}

.percentage {
    margin-right: auto;
    margin-left: auto;
}